
// 默认配置
.ui-button
{
    border-radius: 4px;
    font-size: 16px;
    color: #fff;
    &:hover{
        cursor: pointer;
        color: #fff;
        span{
            text-decoration: none;
        }
    }

    .ui-button-content{

    }

}


.ui-button-block {
  display: block;
}  

.ui-button-100per{
    width: 100%;
}

// 圆角

// 默认背景色
.ui-button-primary{
    background: #4092f1;
    &:hover{
        background: #66a8f4;
    }
}

.ui-button-color2{
    background: #f5f5f5;
    color: #000;
    border: 1px solid #cccccc;
    &:hover{
        background: #fafafa;
        color: #000;
    }
}

