// 大小
.ui-button-size-min{
    height: 30px;
    font-size: 12px;
    .ui-button-content{
        padding: 0px 15px;
    }

    .icon{
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: -4px;
        margin-right: 8px;
    }
    .icon-demo{
        background: #666;
    }

}


.ui-button-size-middle{
    height: 40px;
    .ui-button-content{
        padding: 0px 30px;
    }
}

.ui-button-size-s-large{
    height: 50px;
    .ui-button-content{
        padding: 0px 30px;
    }
}


.ui-button-size-large{
    height: 60px;
    .ui-button-content{
        padding: 0px 50px;
    }
    .icon{
        width: 20px;
        height: 20px;
        margin: 3px auto 2px auto;
    }
    .icon-demo{
        background: #666;
    }
}


